@font-face{
  font-weight: bold;
  font-family: 'Qanelas';
  src:url('./assets/fonts/qanelas-bold.otf');
}

@font-face{
  font-weight: normal;
  font-family: 'Qanelas';
  src:url('./assets/fonts/qanelas-medium.otf');
}

@font-face{
  font-weight: lighter;
  font-family: 'Qanelas';
  src:url('./assets/fonts/qanelas-light.otf');
}

*{
  margin:0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box
}

html,body{
  height: 100% !important;
}

.navbar,.navbar-default{
  background-color: #fff !important;
  border:0 !important;
  padding-top: 5px  !important ;
  padding-bottom:5px !important;
  border-radius: 0 !important;
  -webkit-box-shadow: 0 2px 6px rgba(37, 61, 139, 0.2),2px 3px 8px rgba(29, 62, 153, 0.3);
          box-shadow: 0 2px 6px rgba(37, 61, 139, 0.2),2px 3px 8px rgba(29, 62, 153, 0.3);
}

.navbar-brand{
  font-size: 2em;
  font-weight: bold;
}

.navbar-brand,.navbar-right>li>a{
  color: #0153a9 !important;
}
.navbar-right>li>a{
  font-family: lato;
}

.navbar-default .navbar-toggle {
  border:0 !important;;
  font-size: 2em;
  color:#0153a9;
  background-color: transparent !important
}

/* welcome */
.App{
  width: 100%;
  background-color:#0153a9;
  color:#fff;
  text-align: center;
  height:100vh;
  text-shadow: 0.2px 0.2px 0.2px;
  background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,.4)),to(rgba(0,0,0,0.6))), url('./assets/background.jpg');
  background-image: -webkit-linear-gradient(rgba(0,0,0,.4),rgba(0,0,0,0.6)), url('./assets/background.jpg');
  background-image: -o-linear-gradient(rgba(0,0,0,.4),rgba(0,0,0,0.6)), url('./assets/background.jpg');
  background-image: linear-gradient(rgba(0,0,0,.4),rgba(0,0,0,0.6)), url('./assets/background.jpg');
  background-size: cover;
  font-family: 'Qanelas';
  overflow: hidden;
}

.hero{
  padding:45px;
}

.tagline{
  margin:15vh auto 0;
  font-weight: bolder;
  font-size: 4em;
}

.about{
  margin:0 auto;
  width:60%;
  font-size: 18px;
  font-weight: normal;
  margin:15px auto;
  margin-bottom: 15px !important;
}

.get-started,.get-started:hover,.get-started:focus{
  text-decoration: none;
  color:#fff;
  border:2px solid #fff;
  border-radius:25px;
  -webkit-box-shadow:5px 5px 5px 0  rgba(29, 62, 153, 0.3);
          box-shadow:5px 5px 5px 0  rgba(29, 62, 153, 0.3);
  padding:5px 15px;
  font-size: 1.25em;
  font-weight: bold;
  text-transform: uppercase;
}

/* second page */
.b{
  display: block;
  margin:20vh auto;
  width: 100%;
  color:#fff;
  text-shadow: none;
  background-color:transparent;
  border:0;
  font-weight: bold;
  font-size:18px;
  outline:none;
}

.b:hover,.b:focus{
  text-decoration: none !important;
  color:#fff;
}

.thumbnail{
  display: block;
  margin:15vh auto 0;
  background-color: rgba(0, 0, 0, 0.3);
  padding:2px 5px 2px 5px!important;
  border:3px solid #fff;
  overflow-y: scroll;
  height:60vh;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
.thumbnail::-webkit-scrollbar {
  display: none;
}

.about-h{
  display: block;
  margin:-15px auto 10px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
}
.about-p{
  text-align: left !important;
  width: 100% !important;
  font-size: 18px;
  font-family: lato;
  margin-left:5px;
  font-weight: lighter
}

.solutions-h{
  font-weight: bold;
  text-align: left;
  text-transform: uppercase;
  text-shadow: 0 0 0 ;
}

.home-btn::before{
  content:"Home";
  font-family: 'Qanelas';
}

.about-btn::before{
  content:"About";
  font-family: 'Qanelas';
}

.solutions-btn::before{
  content:"Solutions";
  font-family: 'Qanelas';
}

.services-btn::before{
  content:"Services";
  font-family: 'Qanelas';
}

.products-btn::before{
  content:"Products";
  font-family: 'Qanelas';
}

.contact-btn::before{
  content:"Contact";
  font-family: 'Qanelas';
}

/* contact form */
.form-group{
  margin:10px auto;
  width:80%;
}

.form-control{
  background-color: transparent;
  font-weight: bold;
  border:2px solid #fff;
}
.reach{
  text-align: center !important;
}

.send{
  background-color:#fff;
  color:rgba(0, 0, 0, 0.9);
  border-radius: 5px;
  font-weight: bold;
  border:0;
  font-size: 18px;
  padding:5px 45px;
  margin-bottom: 15px;
}

.below-form>.solutions-h,.below-form>.about-p{
  margin:10px auto 0 !important;
  text-align: center !important;
}

a,a:hover,a:focus{
  text-decoration: none;
  color:#fff;
}

@media screen and (max-width:768px){
  .App{
    height:100vh;
    /* padding-bottom:15px; */
  }
  .hero{
    padding: 10px;
  }
  .tagline{
    font-size: 3em;
    margin:15px auto 0;
  }
  .about{
    width:100%;
    text-align: center;
    white-space: normal
  }
  .bu{
    display: -ms-flexbox !important;
    display: flex !important;
    -ms-flex-align:space-between;
        align-items:space-between;
    margin:0;
  }
  .b{
    margin:15px auto 0;
  }
  .thumbnail{
    margin:15px auto 0;
    height:70vh;
  }
  .navbar,.navbar-default{
    display: none;
  }
  .form{
    width: 100%;
    display: block;
  }
  .form-group{
    width: 100% !important;
    text-align: center;
    margin:10px auto 10px 5px;
    display: block;
  }
}

@media screen and (min-width:1440px){
  .tagline{
    margin-top:25vh;
    font-size: 6em;
  }
  .about{
    font-size: 21px;
    width: 70%;
    margin-bottom: 25px !important;
  }
  .get-started{
    font-size: 21px;
  }
  .App{
    text-shadow: 0 0 0;
  }
  .about-p{
    font-size:21px;
  }
}



